body {
  background-color: #708090;
}

.header {
  text-align: center;
}

.logo {
  cursor: pointer;
  width: 400px;
  height: 400px;
}

.body {
  justify-content: center;
  align-items: center;
  gap: 50px;
  display: flex;
}

#campeones, #mapsShow {
  flex-direction: column;
  gap: 20px;
  width: 80%;
  margin: auto;
  display: flex;
}

.button, .maps {
  color: #000;
  text-align: center;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0, 2), 0 6px 20px 0 rgba(0, 0, 0, 0, 19);
  background: linear-gradient(160deg, #d09c2c 0%, #f0c68f 50%, #d09c2c 100%);
  border: none;
  border-radius: 8px;
  padding: 15px 32px;
  font-family: DejaVu Sans Mono;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.card, .map {
  text-align: center;
}

.card .splash {
  border: 2px solid #d5b868;
  border-radius: 20px;
  width: 100%;
}

.inside {
  color: #e5c878;
  text-shadow: 1px 0 10px #fc0;
  border-radius: 20px;
  font-size: 18px;
  position: relative;
}

.inside .icon {
  border: 2px solid #e5c878;
  border-radius: 20px;
  margin-top: 10px;
}

.card:hover {
  cursor: pointer;
}

.map img {
  border-radius: 20px;
  width: 100%;
}

.map {
  border: 2px solid #e5c878;
  border-radius: 20px;
}

@media (width >= 900px) {
  #campeones, #mapsShow {
    grid-gap: 10px 30px;
    grid-template-columns: repeat(3, 1fr);
    width: 90%;
    margin: auto;
    display: grid;
  }

  .inside {
    color: #0000;
    background-color: #0000;
    display: none;
  }

  .splash {
    cursor: pointer;
    mask-image: linear-gradient(135deg, #000 25%, #ffffff7d 50%, #000 75%);
    mask-size: 500%;
  }

  .card:hover .splash {
    animation: 2s shine;
  }

  .card:hover .inside {
    color: #e5c878;
    text-shadow: 1px 0 10px #fc0;
    animation: 2.5s ease-out showInside;
    display: inline-block;
  }

  @keyframes showInside {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }

  @keyframes shine {
    0% {
      mask-position: 0%;
    }

    100% {
      mask-position: -120%;
    }
  }
}
